'use client'

import { removeTodo } from "app/features/todos/TodoSlice"
import { RootState, useAppDispatch, useAppSelector } from "app/lib/store"

export default function TodoList(){
    const todos = useAppSelector((state: RootState) => state.todos.items)
    const dispatch = useAppDispatch()
    return(
        <div>
            <ul>
                {todos.map((todo) => (
                    <li key={todo.id} >
                        {todo.todo}
                        <button aria-label="Xoa?" onClick={() => dispatch(removeTodo(todo.id))}>Xoa?</button>
                    </li>
                ))}
            </ul>
        </div>
    )
}